.about__container{
    display:flex;
    flex-direction:row-reverse;
    gap:2rem;
    width:60%;
}
.about__me{
    width:100%;
    aspect-ratio: 1/1;
    border-radius:2rem;
    background:linear-gradient(45deg, transparent, var(--color-primary),transparent);
    display:grid;
    place-items:center;
}
.about__me-image:hover{
    transform:rotate(0deg);
}

.about__card small{
    font-size:.7rem;
    color:var(--color-light)
}
.about__content p{
    margin:2rem 0 2.6rem;
    color:var(--color-primary);
}

/* ----------- MEDIA QUERIES (MD DEV) ------------- */
@media screen and (max-width:1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap:0;
    }
    .about__me{
        width:50%;
        margin:2rem auto 4rem;
    }
    .about__content p{
        margin:1rem 0 1.5rem;
    }
}
/* ----------- MEDIA QUERIES (SM DEV) ------------- */
@media screen and (max-width:600px){
    .about__me{
        width:65%;
        margin:0 auto 3rem;
    }
    .about__content{
        text-align:center;
    }
    .about__content p{
        margin:1.5rem 0;
    }
}