#welcome{
    position:fixed;
    top:0;
    left:0;
    height:100vh;
    width:100vw;
    background:var(--color-bg);
    z-index:9;
    display:flex;
    justify-content:center;
    align-items:center;
    margin-top:0 !important;
    transition:all .5s ease;
}
#welcome.hide{
    top:-100%;
}
#welcome h1{
    font-size:7rem;
    font-weight:700;
    line-height:7rem;
}
#welcome h1:nth-child(3), #welcome h1:nth-child(1){
    color: var(--color-bg);
	text-shadow:
   -1px -1px 0 var(--color-primary),  
    1px -1px 0 var(--color-primary),
    -1px 1px 0 var(--color-primary),
     1px 1px 0 var(--color-primary);
}
.welcome__container{
    display:flex;
    flex-direction:column;
}
#welcome p{
    margin:2rem 0;
    width:60%;
}

.welcome__buttons{
    display:flex;
    gap:1rem;
}